import React from 'react';
import {Link} from 'react-router-dom'

import descPhotoOne from '../img/description-1.jpg'
import descPhotoTwo from '../img/description-2.jpg'


const Description = () => (
    <div className="description">
        <div className="description__photos">
            <img src={descPhotoTwo} alt="" className="description__photos--1"/>
            <img src={descPhotoOne} alt="" className="description__photos--2"/>
        </div>
        <div className="description__info">
            <h2 className="heading-2 description__info__title--1">Dowiedz si wicej</h2>
            <h2 className="heading-2 description__info__title--2">O naszej restauracji</h2>
            <p>ChickyChicken to restuaracja znajdujca si w Warszawie.</p>
            <p>W naszym lokalu nie brakuje miejsca, gdzie mona dobrze zje, wypi piwo lub spotka si z przyjacimi. W takiej sytuacji nie trudno o tumy, polecamy <Link to='/reservationPage' className="btn-inline">zarezerwowa sobie stolik</Link>.</p>
            <p>Podajemy zawsze wieo i dobrze bo nasz ide jest jako i zadowolenie naszych klientw. Nasza kuchnia serwuje przede wszystkim pyszne, ogromne i soczyste kurczaki w zocistej panierce, lecz znajdzie si tu rwnie co dla smakoszy innych smakw.</p>
        </div>
    </div>
)

export default Description;




