import React from 'react';
import {Link} from 'react-router-dom'

export const Header = () => (
  <div className="header">
      <h1 className="heading-1 header__title--1">U nas zjesz</h1>
      <h1 className="heading-1 header__title--2">Najlepsze kurczaki!</h1>
      <p className="header__paragraph">Podajemy zawsze wieo i dobrze bo nasz ide jest jako i zadowolenie naszych klientw</p>
      <p className="header__paragraph">Smaymy siedem dni w tygodniu, wyczekuj promocji</p>
      <Link to="/menuPage"><button className="btn header__btn">Zobacz Menu</button></Link>
  </div>
);

export default Header;


